import { useDispatch, useSelector } from "react-redux";
import { add, remove } from "../redux/Slices/CartSlice";
import { Toaster, toast } from "react-hot-toast";

const Product = ({ post }) => {
  const { cart } = useSelector((state) => state);
  const dispatch = useDispatch();

  function addItem() {
    dispatch(add(post));
    toast.success("Item Added to cart");
  }

  function removeItem() {
    dispatch(remove(post.id));
    toast.error("Item removed from cart");
  }

  return (
    <div className="flex flex-col items-center justify-between hover:scale-110 transition duration-200 ease-in gap-3 p-4 mt-10 ml-5 rounded-xl  hover:shadow-[6px_10px_25px_10px_#718096]">
      <div>
        <p className="text-gray-700 font-semibold text-lg text-left truncate w-40 mt-1">
          {post.title}
        </p>
      </div>
      <div>
        <p className="w-40 text-gray-500 font-normal text-[10px] text-left">
          {post.description.split(" ").slice(0, 10).join(" ") + "..."}
        </p>
      </div>
      <div className="h-[180px]">
        <img className="h-full w-full" src={post.image} />
      </div>
      <div className="flex justify-between gap-11 items-center w-full ">
        <div>
          <p className="text-green-500">${post.price}</p>
        </div>
        <div>
          {cart.some((p) => p.id == post.id) ? (
            <button
              className="text-gray-700 border-2 border-gray-700 rounded-full font-semibold text-[10px] p-1 px-3 uppercase hover:bg-gray-700 hover:text-white transition duration-250 ease-in"
              onClick={removeItem}
            >
              Remove Item
            </button>
          ) : (
            <button
              className="text-gray-700 border-2 border-gray-700 rounded-full font-semibold text-[10px] p-1 px-3 uppercase hover:bg-gray-700 hover:text-white transition duration-200 ease-in"
              onClick={addItem}
            >
              Add to Cart
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default Product;
